import React from "react";
import ListGroup from "react-bootstrap/ListGroup";
import Tabs from "react-bootstrap/Tabs";
import Tab from "react-bootstrap/Tab";
import Container from "react-bootstrap/Container";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Button from "react-bootstrap/Button";
import {Form} from "react-bootstrap";
import {connect} from "react-redux";
import {set_tab_selected} from "./actions/profileActions";


class OverviewContainer extends React.Component {

    setKey(k) {
        console.log(k)
        this.props.dispatch(set_tab_selected(k))
    }

    render() {
        return (
            <Container fluid>
                <Row>
                    <Col><h1>{this.props.profile.url}</h1></Col>
                </Row>
                <Row>
                    <Col><Button>Request Data</Button></Col>
                    <Col><Button>Rectify Data</Button></Col>
                    <Col><Button>Delete Data</Button></Col>
                </Row>
                <Row>
                    <Col>
                        <Tabs activeKey={this.props.selected}
                        onSelect={(k) => this.setKey(k)}>
                            <Tab eventKey="Data" title="Data">
                                <Form style={{border:'2px solid #000000'}}>
                                    <Form.Group as={Row}>
                                        <Form.Label column sm="3">
                                            URL
                                        </Form.Label>
                                        <Col sm="9">
                                            <Form.Control plaintext readOnly defaultValue={this.props.profile.url} />
                                        </Col>
                                    </Form.Group>
                                    <Form.Group as={Row}>
                                        <Form.Label column sm="3">
                                            Status
                                        </Form.Label>
                                        <Col sm="9">
                                            <Form.Control plaintext readOnly defaultValue={this.props.profile.status} />
                                        </Col>
                                    </Form.Group>
                                    <Form.Group as={Row}>
                                        <Form.Label column sm="3">
                                            Last communication
                                        </Form.Label>
                                        <Col sm="9">
                                            <Form.Control plaintext readOnly defaultValue={this.props.profile.last_comm} />
                                        </Col>
                                    </Form.Group>
                                    <Form.Group as={Row}>
                                        <Form.Label column sm="3">
                                            Data
                                        </Form.Label>
                                        <Col sm="9">
                                            <Form.Control as="textarea" rows={5} readOnly defaultValue={this.props.profile.data} />
                                        </Col>
                                    </Form.Group>
                                </Form>
                            </Tab>
                            <Tab eventKey="History" title="History">
                                <Form>
                                    {
                                        this.props.profile.interaction
                                            .map(entry => (
                                                <Form.Group as={Row} style={{border:'2px solid #000000'}}>
                                                    <Form.Label column sm="3">
                                                        {entry.date}
                                                    </Form.Label>
                                                    <Col sm="9">
                                                        <Form.Control as="textarea" plaintext readOnly defaultValue={entry.type + " (" + entry.iid + ")\n" + entry.msg}>
                                                        </Form.Control>
                                                    </Col>
                                                </Form.Group>
                                            ))
                                    }

                                </Form>
                            </Tab>
                            <Tab eventKey="contact" title="Contact" disabled>
                                IJKL
                            </Tab>
                        </Tabs>
                    </Col>
                </Row>
            </Container>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        profile: state.profile.profile,
        selected: state.profile.selected,
        loading: state.profile.loading,
    }
}

export default connect(mapStateToProps)(OverviewContainer);